
.section2 {
    background-color: white;
}

.section2-content {
    padding: 3rem 0;
}

.section2-header {
    margin: auto;
    text-align: center;
    padding: .5rem;
}

.section2-header h1 {
    color: var(--primary-text);
    font-family: 'Nanum Myeongjo', serif;
}

.section2-buttons {
    display: flex;
    flex-wrap: wrap;
    justify-content: center;
    gap: 1rem;
    padding: 2rem 0;
}

.section2-button {
    background-color: rgb(36, 149, 214);
    padding: 1.2rem 5rem;
    border-radius: 5px;
    cursor: pointer;
    transition: border-radius .5s;
}

.section2-button:hover {
    background-color: rgb(214, 143, 36);
    border-top-left-radius: 1.5rem;
    border-bottom-right-radius: 1.5rem;
}

.section2-button h1 {
    text-transform: uppercase;
    font-size: 1rem;
    color: white;
    letter-spacing: 1px;
}

.section2-content p {
    text-align: center;
    font-size: 1.2rem;
}

.section2-subheader {
    margin: auto;
    width: 50%;
    padding: 1rem;
}

.section2-subheader p {
    font-family: 'Nanum Myeongjo', serif;
    font-size: 1.5rem;
}

/* FLEX */

.section2-flex {
    display: flex;
    justify-content: center;
    gap: 1rem;
    padding: 5rem 0;
}

.section2-left {
    width: 100%;
    margin: auto;
    padding: 1rem;
}

.section2-left h1 {
    font-size: 2rem;
    padding: .5rem 1rem;
    letter-spacing: 2px;
    font-weight: 600;
    font-family: 'Indie Flower', cursive;

}

.section2-left p {
    font-size: 16px;
    text-align: justify;
    padding: 0 1rem;
    color: rgb(17, 17, 17);
    letter-spacing: 2px;

}


.section2-right {
    padding: 1rem;
    margin: auto;
    width: 100%;
    height: 100%;
}

.section2-images {
    width: 100%;;
    margin: auto;
    position: relative;
}

.section2-right img {
    width: 100%;
    border-radius: 2rem;
    padding: 1rem;
}

.section2-accent {
    position: absolute;
    width: 30%;
    top: 0%;
    left: 0;
}

.section2-accent2 {
    position: absolute;
    width:100%;
    bottom: -20%;
    left: 0;
    opacity: 0.8;
}


.section2-accent img {
    width: 100%;
}

@media screen and (max-width:940px) {
    .section2-left {
        width: 90%;
    }

    .section2-flex {
        flex-wrap: wrap;
    }

    .section2-subheader {
        width: 90%;
    }

}

@media screen and (max-width:700px) {
    .section2-header h1 {
        font-size: 1.2rem;
    }

  }